.p {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(201, 197, 197);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
  }

  .p-browser{
    height: 20px;
    background-color: rgb(201, 197, 197);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
  }
.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.p-img{
  width: 100%;
  height: 110%;
  transition: all 10s ease;
}

.p:hover .p-img {
  transform: translateY(-10%);
}

@media screen and (max-width: 480px) {
  .p {
    width: 40%;
    height: 18vh;
  }
}